export default class AuthorGambit {
  pattern() {
    return 'author:(.+)';
  }
  toFilter(matches, negate) {
    const key = (negate ? '-' : '') + 'author';
    return {
      [key]: matches[1].split(',')
    };
  }
  filterKey() {
    return 'author';
  }
  fromFilter(value, negate) {
    return "".concat(negate ? '-' : '', "author:").concat(value);
  }
}
flarum.reg.add('core', 'common/query/discussions/AuthorGambit', AuthorGambit);