export default class UnreadGambit {
  pattern() {
    return 'is:unread';
  }
  toFilter(_matches, negate) {
    const key = (negate ? '-' : '') + 'unread';
    return {
      [key]: true
    };
  }
  filterKey() {
    return 'unread';
  }
  fromFilter(value, negate) {
    return "".concat(negate ? '-' : '', "is:unread");
  }
}
flarum.reg.add('core', 'common/query/discussions/UnreadGambit', UnreadGambit);