export default class EmailGambit {
  pattern() {
    return 'email:(.+)';
  }
  toFilter(matches, negate) {
    const key = (negate ? '-' : '') + 'email';
    return {
      [key]: matches[1]
    };
  }
  filterKey() {
    return 'email';
  }
  fromFilter(value, negate) {
    return "".concat(negate ? '-' : '', "email:").concat(value);
  }
}
flarum.reg.add('core', 'common/query/users/EmailGambit', EmailGambit);