export default class CreatedGambit {
  pattern() {
    return 'created:(\\d{4}\\-\\d\\d\\-\\d\\d(?:\\.\\.(\\d{4}\\-\\d\\d\\-\\d\\d))?)';
  }
  toFilter(matches, negate) {
    const key = (negate ? '-' : '') + 'created';
    return {
      [key]: matches[1]
    };
  }
  filterKey() {
    return 'created';
  }
  fromFilter(value, negate) {
    return "".concat(negate ? '-' : '', "created:").concat(value);
  }
}
flarum.reg.add('core', 'common/query/discussions/CreatedGambit', CreatedGambit);