export default class GroupGambit {
  pattern() {
    return 'group:(.+)';
  }
  toFilter(matches, negate) {
    const key = (negate ? '-' : '') + 'group';
    return {
      [key]: matches[1].split(',')
    };
  }
  filterKey() {
    return 'group';
  }
  fromFilter(value, negate) {
    return "".concat(negate ? '-' : '', "group:").concat(value);
  }
}
flarum.reg.add('core', 'common/query/users/GroupGambit', GroupGambit);