export default class HiddenGambit {
  pattern() {
    return 'is:hidden';
  }
  toFilter(_matches, negate) {
    const key = (negate ? '-' : '') + 'hidden';
    return {
      [key]: true
    };
  }
  filterKey() {
    return 'hidden';
  }
  fromFilter(value, negate) {
    return "".concat(negate ? '-' : '', "is:hidden");
  }
}
flarum.reg.add('core', 'common/query/discussions/HiddenGambit', HiddenGambit);